/*home page design*/
.recipesSelection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
}

.selectionHeading {
    font-size: 4rem;
    margin-bottom: 0;
}

.selections {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.label,
.menuLabel {
    font-family: 'Handlee', cursive;
    font-size: 3rem;
    margin: 0;
    padding-top: 2rem;
}

.morning,
.lunch,
.afternoon {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 33.3%;
}

.morning {
    background-color: #b2f7ef;
}

.lunch {
    background-color: #f2b5d4;
}

.afternoon {
    background-color: #f8e16c;
}

.meal {
    margin-top: 4rem;
    width: 100%;
}


/*Responsive*/
@media screen and (min-width:451px) and (max-width:800px) {
    .recipesSelection {
        margin-top: 0;
    }

    .selectionHeading {
        font-size: 2.2rem;
    }

    .selections {
        flex-direction: column;
        height: max-content;
    }

    .label {
        writing-mode: vertical-rl;
        font-size: 2rem;
        padding: 0;
    }

    .morning,
    .lunch,
    .afternoon {
        flex-direction: row;
        justify-content: center;
        width: 100%;
    }
}

@media screen and (max-width:450px) {
    .recipesSelection {
        margin-top: 0;
    }

    .selectionHeading {
        font-size: 2.2rem;
    }

    .selections {
        flex-direction: column;
        height: max-content;
    }

    .label {
        writing-mode: vertical-rl;
        font-size: 1.5rem;
        padding: 0;
    }

    .morning,
    .lunch,
    .afternoon {
        flex-direction: row;
        justify-content: center;
        width: 100%;
    }
}